<template>
  <ik-data-table
    :model="model"
    :entity-name="$t('人员')"
    :icon="'mdi-account'"
  >
    <template v-slot:footer>
      <v-btn
        class="ml-2 mr-2"
        style="color: white"
        dark
        @click="dialog = true"
      >
        {{ $t('打印跑堂日结单') }}
        <v-icon
          dark
          right
        >
          mdi-plus
        </v-icon>
      </v-btn>
      <v-dialog
        v-model="dialog"
        max-width="500px"
      >
        <v-card>
          <v-card-title>
            <span class="text-h5">{{ $t('打印跑堂日结单') }}</span>
          </v-card-title>
          <v-card-text>
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="dates"
              offset-y
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dates"
                  hide-details
                  :label="$t('日期筛选')"
                  prepend-icon="mdi-calendar"
                  single-line
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="dates"
                no-title
                range
                scrollable
                style="width: 100%"
                locale="de"
              >
                <v-spacer/>
                <v-btn
                  text
                  color="primary"
                  @click="menu = false"
                >
                  {{ $t('Cancel') }}
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.menu.save(dates)"
                >
                  {{ $t('Ok') }}
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn
              color="blue darken-1"
              text
              @click="dialog = false"
            >
              {{ $t('Cancel') }}
            </v-btn>
            <v-btn
              color="blue darken-1"
              @click="printAllServantBon"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>
  </ik-data-table>
</template>

<script>
  import GlobalSettings from '@/GlobalSettings'
  import IKUtils from 'innerken-js-utils'
  import { IkDataTable } from 'metaflow-js'
  import Servant from '../../model/Servant'
  import { printServantSummaryBon } from '../../components/Utlis/api'
  import { formatDate } from '../../common/common'

  export default {
    components: { IkDataTable },
    data: function () {
      return ({
        model: Servant,
        dates: [],
        menu: false,
        dialog: false,
      })
    },
    computed: {
      ImageRoot () {
        return this.$store.getters.rootUrl + GlobalSettings.servantRoot
      },
    },
    mounted () {
      this.dates = [formatDate(0), formatDate(0)]
    },
    methods: {
      clear () {
        this.dates = []
      },
      async printAllServantBon () {
        if (this.dates[1] === undefined) {
          this.dates[1] = this.dates[0]
        }
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        const servantArr = (await Servant.getList())
        servantArr.forEach(d => {
          printServantSummaryBon(d.id, this.dates[0], this.dates[1]).then(() => {
            IKUtils.toast()
            this.menu = false
            this.dialog = false
          })
        })
      },
      goNext (item) {
        this.$router.push({
          name: 'Keller Detail Seite',
          params: { id: item.id },
        })
      },
    },
  }
</script>
